	module.exports = {"data":{"postsJson":{"title":"The Mission in Galilee Completed","questionsTitle":"LEARNING FROM THE PAST","display":"block","bookName":"Luke","ref":"9:1-9:50","html":"<p>   <strong>9</strong><u>1</u>He called the twelve* together, and gave them power and authority over all demons, and to cure diseases. <u>2</u>He sent them out to preach God’s Kingdom and to heal the sick. <u>3</u>He said to them,</p><h1>JESUS</h1><h2>Take nothing for your journey—neither staffs, nor wallet, nor bread, nor money; neither have two coats apiece. <u>4</u>Into whatever house you enter, stay there, and depart from there. <u>5</u>As many as don’t receive you, when you depart from that city, shake off even the dust from your feet for a testimony against them.</h2><p>   <u>6</u>They departed, and went throughout the villages, preaching the Good News, and healing everywhere. <u>7</u>Now Herod the tetrarch heard of all that was done by him; and he was very perplexed, because it was said by some that John had risen from the dead, <u>8</u>and by some that Elijah had appeared, and by others that one of the old prophets had risen again. <u>9</u>Herod said,</p><h1>HEROD ANTIPAS</h1><h4>John I beheaded, but who is this, about whom I hear such things?</h4><p>He sought to see him. <u>10</u>The apostles, when they had returned, told him what things they had done.<br />   He took them, and withdrew apart to a deserted place of a city called Bethsaida. <u>11</u>But the multitudes, perceiving it, followed him. He welcomed them, and spoke to them of God’s Kingdom, and he cured those who needed healing. <u>12</u>The day began to wear away; and the twelve came, and said to him,</p><h1>THE DISCIPLES</h1><h3>Send the multitude away, that they may go into the surrounding villages and farms, and lodge, and get food, for we are here in a deserted place.</h3><p>   <u>13</u>But he said to them,</p><h1>JESUS</h1><h2>You give them something to eat.</h2><p>   They said,</p><h1>THE DISCIPLES</h1><h3>We have no more than five loaves and two fish, unless we should go and buy food for all these people.</h3><p><u>14</u>For they were about five thousand men.<br />   He said to his disciples,</p><h1>JESUS</h1><h2>Make them sit down in groups of about fifty each.</h2><p><u>15</u>They did so, and made them all sit down. <u>16</u>He took the five loaves and the two fish, and looking up to the sky, he blessed them, and broke them, and gave them to the disciples to set before the multitude. <u>17</u>They ate, and were all filled. They gathered up twelve baskets of broken pieces that were left over.<br />   <u>18</u>As he was praying alone, the disciples were with him, and he asked them,</p><h1>JESUS</h1><h2>Who do the multitudes say that I am?</h2><p>   <u>19</u>They answered,</p><h1>THE DISCIPLES</h1><h3>‘John the Baptizer,’ but others say, ‘Elijah,’ and others, that one of the old prophets is risen again.</h3><p>   <u>20</u>He said to them,</p><h1>JESUS</h1><h2>But who do you say that I am?</h2><p>   Peter answered,</p><h1>SIMON PETER</h1><h3>The Christ of God.</h3><p>   <u>21</u>But he warned them, and commanded them to tell this to no one, <u>22</u>saying,</p><h1>JESUS</h1><h2>The Son of Man must suffer many things, and be rejected by the elders, chief priests, and scribes, and be killed, and the third day be raised up.</h2><p>   <u>23</u>He said to all,</p><h1>JESUS</h1><h2>If anyone desires to come after me, let him deny himself, take up his cross,* and follow me. <u>24</u>For whoever desires to save his life will lose it, but whoever will lose his life for my sake, the same will save it. <u>25</u>For what does it profit a man if he gains the whole world, and loses or forfeits his own self? <u>26</u>For whoever will be ashamed of me and of my words, of him will the Son of Man be ashamed, when he comes in his glory, and the glory of the Father, and of the holy angels. <u>27</u>But I tell you the truth: There are some of those who stand here, who will in no way taste of death, until they see God’s Kingdom.</h2><p>   <u>28</u>About eight days after these sayings, he took with him Peter, John, and James, and went up onto the mountain to pray. <u>29</u>As he was praying, the appearance of his face was altered, and his clothing became white and dazzling. <u>30</u>Behold, two men were talking with him, who were Moses and Elijah, <u>31</u>who appeared in glory, and spoke of his departure,* which he was about to accomplish at Jerusalem.<br />   <u>32</u>Now Peter and those who were with him were heavy with sleep, but when they were fully awake, they saw his glory, and the two men who stood with him. <u>33</u>As they were parting from him, Peter said to Jesus,</p><h1>SIMON PETER</h1><h3>Master, it is good for us to be here. Let’s make three tents: one for you, and one for Moses, and one for Elijah,</h3><p>not knowing what he said.<br />   <u>34</u>While he said these things, a cloud came and overshadowed them, and they were afraid as they entered into the cloud. <u>35</u>A voice came out of the cloud, saying,</p><h1>GOD</h1><h2>This is my beloved Son. Listen to him!</h2><p><u>36</u>When the voice came, Jesus was found alone. They were silent, and told no one in those days any of the things which they had seen.<br />   <u>37</u>On the next day, when they had come down from the mountain, a great multitude met him. <u>38</u>Behold, a man from the crowd called out, saying,</p><h1>AN EPILEPTIC’S FATHER</h1><h4>Teacher, I beg you to look at my son, for he is my only child. <u>39</u>Behold, a spirit takes him, he suddenly cries out, and it convulses him so that he foams, and it hardly departs from him, bruising him severely. <u>40</u>I begged your disciples to cast it out, and they couldn’t.</h4><p>   <u>41</u>Jesus answered,</p><h1>JESUS</h1><h2>Faithless and perverse generation, how long shall I be with you and bear with you? Bring your son here.</h2><p>   <u>42</u>While he was still coming, the demon threw him down and convulsed him violently. But Jesus rebuked the unclean spirit, and healed the boy, and gave him back to his father. <u>43</u>They were all astonished at the majesty of God.<br />   But while all were marveling at all the things which Jesus did, he said to his disciples,</p><h1>JESUS</h1><h2><u>44</u>Let these words sink into your ears, for the Son of Man will be delivered up into the hands of men.</h2><p><u>45</u>But they didn’t understand this saying. It was concealed from them, that they should not perceive it, and they were afraid to ask him about this saying.<br />   <u>46</u>There arose an argument among them about which of them was the greatest. <u>47</u>Jesus, perceiving the reasoning of their hearts, took a little child, and set him by his side, <u>48</u>and said to them,</p><h1>JESUS</h1><h2>Whoever receives this little child in my name receives me. Whoever receives me receives him who sent me. For whoever is least among you all, this one will be great.</h2><p>   <u>49</u>John answered,</p><h1>JOHN THE DISCIPLE</h1><h3>Master, we saw someone casting out demons in your name, and we forbade him, because he doesn’t follow with us.</h3><p>   <u>50</u>Jesus said to him,</p><h1>JESUS</h1><h2>Don’t forbid him, for he who is not against us is for us.</h2><h5>*Luke 9:1 TR reads “his twelve disciples” instead of “the twelve”<br />*Luke 9:23 TR, NU add “daily”<br />*Luke 9:31 literally, “exodus”</h5>","path":"/330","Question1":"With which character do you most identify in this passage? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this passage?","Question3":"What do you think God was wanting do in the life of your selected character?","Question4":"What do you think God is wanting to do in your life as you reflect on this passage? How can you cooperate with his loving purposes this week?","prevPath":"/329","nextPath":"/331"}},"pathContext":{}}

